/* ------------------------------------------------------------ *\
    Header
\* ------------------------------------------------------------ */

.header {
    position: fixed !important;
    width: 100%;
    left: 0;
    right: 0;
    z-index: 11;
    top:0;
    &.mobile {
        background: $white;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12),0 2px 4px 0 rgba(0,0,0,0.08);
    }
    & .header_wrapper {
        & .logo {
            display: inline-block;
            width: 100px;
            position: absolute;
            left: 0;
            right: 0;
            top: 10px;
            margin: auto;
        }
        & .logo-fixed {
            display: none;
            top: 10px;
        }
    }
    position: relative;
    z-index: 99;
    height: 66px;
}
.header_wrapper {
    // padding: 15px 0;
    // padding-top: 20px;
    overflow: hidden;
    height: 66px;
}
.logo,
.logo-fixed {
    // float: left;
    float: none;
}
.social_icons-header {
    display: none;
}
.list_icons_header_mobile,
.lang_list_mobile {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    & li {
        padding-right: 5px;
        padding-left: 5px;
    }
}

.header.header-white {
    background: $white;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12),0 2px 4px 0 rgba(0,0,0,0.08);
    & .header_wrapper {
        // padding-bottom: 20px;
        // padding: 15px 0;
        & .logo {
            display: none;
        }
        & .logo-fixed {
            display: inline-block;
            width: 100px;
            position: absolute;
            left: 0;
            right: 0;
            margin: auto;
        }
        & .nav {
            color: $darkgray1;
            &.active {
                color: $white;
            }
            & .menu a:hover {
                color: $green2;
            }
        }
        & .nav_trigger {
            width: 18px;
            height: 18px;
            & span {
                // background: $darkgray1;
                background: $green;
            }
            // &.active{
            //     & span {
            //         background: $white;
            //     }
            // }
        }
        & .lang_bar {
            color: $darkgray1;
            & .selected {
                font-size: 16px;
                line-height: 18px;
                font-weight: 400;
                position: relative;
                margin-right: 5px;
                &:hover {
                    color: $green2;
                }
            }
            & .lang_list {
                position: absolute;
                display: none;
                width: 30px;
                line-height: 25px;
                top: 85px;
                & a {
                    display:block;
                }
                &:hover {
