                background-repeat: no-repeat;
                display: inline-block;
                width: 27px;
                height: 27px;
                &:hover {
                    background-image: url("../css/images/green-2.png");
                }
            }
            & .social-3 {
                background-image: url("../css/images/gray-3.png");
                background-repeat: no-repeat;
                display: inline-block;
                width: 27px;
                height: 27px;
                &:hover {
                    background-image: url("../css/images/green-3.png");
                }
            }
            & .social-4 {
                background-image: url("../css/images/gray-4.png");
                background-repeat: no-repeat;
                display: inline-block;
                width: 27px;
                height: 27px;
                &:hover {
                    background-image: url("../css/images/green-4.png");
                }
            }
            & .social-5 {
                background-image: url("../css/images/gray-5.png");
                background-repeat: no-repeat;
                display: inline-block;
                width: 27px;
                height: 27px;
                &:hover {
                    background-image: url("../css/images/green-5.png");
                }
            }
            & .social-6 {
                background-image: url("../css/images/gray-6.png");
                background-repeat: no-repeat;
                display: inline-block;
                width: 27px;
                height: 27px;
                &:hover {
