                background-repeat: no-repeat;
                display: inline-block;
                width: 27px;
                height: 27px;
                &:hover {
                    background-image: url("../css/images/green-7.png");
                }
            }
            & .social-8 {
                background-image: url("../css/images/gray-8.png");
                background-repeat: no-repeat;
