    bottom: -1px;
    left: -1px;
    right: -1px;
    transform: scale(0);
    opacity: 0;
    border-radius: 50%;
    background-color: #54bc79;
    transition: all .3s;
  }
  .dot-nav--item:after {
    position: absolute;
    display: block;
    width: 3px;
    height: 55px;
    margin: -8px auto;
