/* ------------------------------------------------------------ *\
    Nav
\* ------------------------------------------------------------ */
.nav {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  transform: translateX(-100%);
  transition: ease-in-out .3s;
  width: 50%;
  height: 100vh;
  text-align: center;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  z-index: 3;
  font-size: 19px;
  font-weight: 400;
  color: #ffffff;
}

.nav .menu {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  padding: 70px 0 0 40px;
}

.nav .menu li {
  text-align: left;
  padding-bottom: 20px;
  cursor: pointer;
}

.nav .menu a:hover {
  color: #faee49;
}

.nav.active {
  visibility: visible;
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
  background: #0a9372;
}

.nav_trigger {
  float: right;
  margin-top: 23px;
  position: relative;
  display: inline-block;
  z-index: 4;
  width: 28px;
  height: 28px;
}

.nav_trigger span {
  position: absolute;
  display: block;
  width: 100%;
  height: 4px;
  border-radius: 0;
  background: #0a9372;
  transition: top .2s .25s, left .2s .25s, opacity .2s .25s, -webkit-transform .2s 0s;
  transition: top .2s .25s, left .2s .25s, opacity .2s .25s, transform .2s 0s;
  transition: top .2s .25s, left .2s .25s, opacity .2s .25s, transform .2s 0s, -webkit-transform .2s 0s;
}

.nav_trigger span:nth-child(1) {
  top: 0;
}

.nav_trigger span:nth-child(2) {
  top: 7px;
}

.nav_trigger span:nth-child(3) {
  top: 14px;
}

.nav_trigger.active span {
  transition: background .2s, top .2s, left .2s, opacity .2s, -webkit-transform .2s .25s;
  transition: background .2s, top .2s, left .2s, opacity .2s, transform .2s .25s;
  transition: background .2s, top .2s, left .2s, opacity .2s, transform .2s .25s, -webkit-transform .2s .25s;
}

.nav_trigger.active span:nth-child(3),
.nav_trigger.active span:nth-child(1) {
  top: 12px;
  background: #0a9372;
}

.nav_trigger.active span:nth-child(2) {
  opacity: 0;
}

.nav_trigger.active span:nth-child(1) {
  transform: rotate(45deg);
}

.nav_trigger.active span:nth-child(3) {
  transform: rotate(-45deg);
}

@media only screen and (min-width: 1200px) {
  .nav_trigger {
    display: none;
  }
  .nav {
    position: static;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
    width: auto;
    height: auto;
    float: left;
    margin-left: 60px;
  }
  .nav .menu {
