  font-family: "Cairo", sans-serif;
  font-weight: 400;
  margin-left: 5px;
}

.slider_wrapper {
  background: #ffffff;
  border-radius: 5px;
  padding: 60px 100px 46px 60px;
  z-index: 1;
}

.ui-widget-content {
  background: #0a9372;
  height: 5px;
  border: none;
  border-radius: none !important;
}

.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  line-height: 30px;
  text-align: center;
  background-color: #0a9372;
  border: 3px solid white;
  background-image: url("../css/images/arrow-direction.png");
  background-repeat: no-repeat;
  display: inline-block;
  background-position: center center;
  outline: 0;
  box-shadow: 0 0 0 0 rgba(10, 147, 114, 0.1);
  -webkit-animation: pulse 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
  -moz-animation: pulse 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
  -ms-animation: pulse 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
  animation: pulse 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
}

@-webkit-keyframes pulse {
  to {
    box-shadow: 0 0 0 45px rgba(101, 212, 166, 0);
  }
}

@-moz-keyframes pulse {
  to {
    box-shadow: 0 0 0 45px rgba(101, 212, 166, 0);
  }
}

@-ms-keyframes pulse {
  to {
    box-shadow: 0 0 0 45px rgba(101, 212, 166, 0);
  }
}

@keyframes pulse {
  to {
    box-shadow: 0 0 0 45px rgba(101, 212, 166, 0);
  }
}

.ui-slider-horizontal .ui-slider-handle {
  top: -13px;
}

.ui-slider-pips .ui-slider-line {
  background: #0a9372;
  width: 5px;
  height: 25px;
  position: absolute;
  left: 50%;
  margin-top: -20px;
}

.ui-slider-pips .ui-slider-label {
  top: 20px;
  font-size: 26px;
  color: #05bc70;
  font-weight: 400;
}

.ui-slider-pips .ui-slider-label:hover {
  color: #05bc70 !important;
  font-weight: 400 !important;
}

.ui-slider-pips:not(.ui-slider-disabled) .ui-slider-pip:hover .ui-slider-label {
  color: #05bc70 !important;
  font-weight: 400 !important;
}

.ui-slider-pips [class*=ui-slider-pip-selected] .ui-slider-line, .ui-slider-pips .ui-slider-pip-inrange .ui-slider-line {
  background: #0a9372 !important;
}

.ui-corner-all, .ui-corner-bottom, .ui-corner-right, .ui-corner-br {
  border-bottom-right-radius: 0;
}

.ui-corner-all, .ui-corner-bottom, .ui-corner-left, .ui-corner-bl {
  border-bottom-left-radius: 0;
}

.ui-corner-all, .ui-corner-top, .ui-corner-right, .ui-corner-tr {
  border-top-right-radius: 0;
}

.ui-corner-all, .ui-corner-top, .ui-corner-left, .ui-corner-tl {
  border-top-left-radius: 0;
}

@media only screen and (min-width: 768px) {
  .slider_wrapper_mobile {
    display: none;
  }
  .slider_wrapper {
    display: block;
  }
}

@media only screen and (min-width: 1024px) {
  .slider_wrapper {
    padding: 63px 71px 63px 33px;
  }
  .flight_bullets li {
    margin-right: -13px;
  }
  .ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default {
    background: #ffc200;
  }
  .slider-question .slider-question-wrapper .slider-left .min {
    color: #ffc200;
  }
  .slider-question .slider-question-wrapper .slider-right .currency {
    color: #ffc200;
  }
  .slider-question .slider-question-wrapper .green-label,
  .slider-question .slider-question-wrapper .euro-label {
    color: #ffc200;
  }
}

@media only screen and (min-width: 1366px) {
  .flight_bullets li {
    margin-right: 5px;
  }
}
